*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    font-size: 62.5%;
    font-family: 'Open Sans', sans-serif;
}

body{
    font-family: inherit;
}

:root{
    --color-white: #fff;
    --color-light-white: #eee;

    --color-tomato: #d40640;
    --color-purple-transparent: rgba(88, 24, 69, .9);
    --color-pink-transparent: rgba(235, 14, 91, .9);

    --color-orange: #ff3c00;

    --color-light-black: #222;
    --color-black: #000;

    --shadow: rgba(0, 0, 0, .4);
}

.app {
    height: 100vh;
    width: 100%;
    background-image: linear-gradient(to bottom right, var(--color-white-transparent), var(--color-purple-transparent));
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}

.calculator{
    height: 80vh;
    width: 25%;
    box-shadow: 0 2rem 4rem var(--shadow);
    background-color: var(--color-white);
}

.result-screen {
    height: 19vh;
    font-size: 8rem;
    font-weight: 600;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}

.computation-screen{
    height: 6vh;
    font-size: 2.5rem;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}

.screen {
    height: 25vh;
    width: 100%;
    padding: 0 2rem;
    overflow: hidden;
    white-space: no-wrap;
    color: var(-color-light-white);
    background-color: #AACB73;
}

.keypad__row{
    height: 11vh;
    width: 100%;
    display: flex;
    color: white;
}

.keypad{
    height: 55vh;
    width: 100%;
}

.btn {
    width: 25%;
    height: 100%;
    font-size: 3rem;
    font-family: inherit;
    background-color: var(--color-white);
    border: 1px solid transparent;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .2s;
  }
  .btn:hover {
    color: var(--color-light-red);
    background-color: #CDE990;
  }
  .btn:active {
    transform: scale(.95);
  }

  .btn--large{
    width: 50%;
    color: var(--color-white);
    background-color: #AACB73;
    transition: all .2s;
  } 

  .btn--large:hover{
    background-color: var(--color-light-red);
    color: var(--color-tomato);
  }

  .btn--primary{
    color: var(--color-tomato);
  }

  .btn--operator{
    color: var(--color-tomato);
  }